import { Document, Leaf, Mark, Text, Value } from '@ali/4ever-cangjie';
import{ Paragraph } from '@ali/4ever-plugin-paragraph';

export default Value.create({
  document: Document.create({
    nodes: ['bold', 'italic', 'strike'].map((type) => {
      return Paragraph.create({
        nodes: [
          Text.create({
            leaves: [
              Leaf.create({
                text: type,
                marks: [Mark.create(type)],
              }),
            ],
          }),
        ],
      });
    }),
  }),
});
